import React, { useState } from "react";
import image from "./title1.png";
import giff from "./think.gif";

function CreateArea(props) {
  const [note, setNote] = useState({
    title: "",
    content: ""
  });

  function handleChange(event) {
    const { name, value } = event.target;

    setNote(prevNote => {
      return {
        ...prevNote,
        [name]: value
      };
    });
  }

  function submitNote(event) {
    props.onAdd(note);
    setNote({
      title: "",
      content: ""
    });
    event.preventDefault();
  }
  return (
    <div>
    <img src={giff} className="giff"></img>
    <img src={image} className="head"></img>
    <h1 className="message">Don't <mark>Forget</mark> things! Have a <mark>Note</mark> for it..<i class="fas fa-smile-wink"></i></h1>
      <form>
        <input
          name="title"
          onChange={handleChange}
          value={note.title}
          placeholder="Title"
        />
        <textarea
          name="content"
          onChange={handleChange}
          value={note.content}
          placeholder="Take a note..."
          rows="3"
        />
        <button onClick={submitNote}><i class="fas fa-plus"></i></button>
      </form>
    </div>
  );
}

export default CreateArea;
